ón on el passat, el misteri i l'acció convergeixen per oferir-nos una història que mai deixarem d'estimar. ")])]),_c('div',{staticClass:"poster"},[_c('img',{attrs:{"src":require("./assets/posterindi.png"),"alt":"poster indiana jones"}})])])
