import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Avengers from '../views/Avengers.vue'
import StarWars from '../views/StarWars.vue'
import IndianaJones from '../views/IndianaJones.vue'
import Madmax from '../views/Madmax.vue'
import HarryPotter from '../views/HarryPotter.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/starwars',
    name: 'starwars ',
    component: StarWars
  },
  {
    path: '/avengers',
    name: 'avengers ',
    component: Avengers
  },
  {
    path: '/indianajones',
    name: 'indianajones ',
    component: IndianaJones
  },
  {
    path: '/harrypotter',
    name: 'harrypotter ',
    component: HarryPotter
  },
  {
    path: '/Madmax',
    name: 'madmax ',
    component: Madmax
  }
  
]

const router = new VueRouter({
  mode: "history",
  base: '/p6/', /*nombre de la carpeta donde se dirigirá github */
  routes
})

export default router