consegueixen arribar a l’Estrella de la Mort, on la princesa Leia està empresonada. En un rescat temerari, Luke, Han, Chewbacca i Obi-Wan s’infiltren a l’estació, destrueixen diversos soldats imperials i alliberen Leia. Obi-Wan, mentrestant, s’enfronta al seu antic aprenent Darth Vader en un duel de sabres làser. Durant el combat, Obi-Wan es deixa sacrificar per permetre que els altres escapin. Amb l’ajuda dels plànols de l’Estrella de la Mort, els rebels descobreixen un punt feble a l’estació: una petita obertura que, si és atacada amb precisió, pot provocar una reacció en cadena i destruir tota l’estructura. Luke s’uneix a l’atac final liderat per l’Aliança Rebel, mentre Han Solo sembla marxar per buscar la seva recompensa. Durant la batalla, els caçabombarders rebels ataquen l’Estrella de la Mort, però són superats pels pilots imperials. Luke, fent servir la Força sota la guia espiritual d’Obi-Wan, llança un tir precís que destrueix l’arma imperial just abans que aquesta pugui aniquilar la base rebel. Han Solo retorna en el moment clau, salvant Luke de ser abatut per Vader. Amb l’Estrella de la Mort destruïda, l’Aliança Rebel celebra una gran victòria. Luke i Han són condecorats com a herois, mentre que l’Imperi queda greument debilitat. Però la guerra entre el bé i el mal està lluny d’acabar. ")])]),_c('div',{staticClass:"poster"},[_c('img',{attrs:{"src":require("./assets/posterstarwars.png"),"alt":"poster star wars"}})])]),_c('iframe',{staticClass:"video",attrs:{"src":"https://www.youtube.com/embed/jRaEj4PVFXY?si=-YYzLpFXl1CdtPaP","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share","referrerpolicy":"strict-origin-when-cross-origin","allowfullscreen":""}})])
